<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <div class="row pb-4">
                <h5 class="text-secondary">Modificación en la asignación de abogados a expedientes específicos</h5>
            </div>
            <div class="row">
                <div class="col-xl-12">
                    <div class="card shadow">
                        <div class="card-body">
                            <div class="row">
                                <div class="col-md-4 col-sm-12">
                                    <div class="form-group">
                                        <label class="form-label">Abogados con expedientes asignados</label>
                                        <select class="form-control fade-transition" @change="listFiles()" v-model="assignedAttorney">
                                            <option value="default_value" disabled selected>Seleccionar</option>
                                            <option v-for="file in lawyersWithFiles" :value="file.abo_id">
                                                {{ completeNames(file) }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-md-4 col-sm-12">
                                    <div class="form-group">
                                        <label class="form-label">Nuevo abogado a asignar a los expedientes</label>
                                        <select class="form-control fade-transition" v-model="newlawyer">
                                            <option value="default_value" disabled selected>Seleccionar</option>
                                            <option v-for="lawyer in lawyers" :value="lawyer.abo_id">
                                                {{ completeNames(lawyer) }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-md-4 col-sm-4" style="margin-top: 27.5px !important">
                                    <div class="d-flex justify-content-end">
                                        <button class="btn btn-success px-4 rounded btn-lg" @click="saveChanges">Guardar</button>
                                    </div>
                                </div>
                            </div>
                            <div class="table-responsive">
                                <table class="table fade-transition" id="miTabla">
                                    <thead>
                                        <tr>
                                            <!-- <th>#</th> -->
                                            <th>N° de Expediente</th>
                                            <th>Fecha de Inicio</th>
                                            <th>Demandante</th>
                                            <th>Demandado</th>
                                            <th>Pretensión</th>
                                            <th>Materia</th>
                                            <th>Estado</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="file in files" :key="file.exp_id">
                                            <!-- <td>
                                                <input type="checkbox" class="form-check-input">
                                            </td> -->
                                            <td>
                                                <router-link
                                                    :to="{ name: 'expediente/detalle', params: { id: file.exp_id } }">
                                                    {{ file.numero }}
                                                </router-link>
                                            </td>
                                            <td>{{ file.fecha_inicio }}</td>
                                            <td class="col-md-1" v-if="file.procesal === 'demandante'">{{
                                                getPersonaName(file) }}
                                            </td>
                                            <td>UNPRG</td>
                                            <td v-if="file.procesal === 'demandado'">{{ getPersonaName(file) }}
                                            </td>
                                            <td>{{ file.pretencion }}</td>
                                            <td>{{ file.materia }}</td>
                                            <td>
                                                <span :class="badgeClass(file.estado_proceso)">
                                                    {{ file.estado_proceso }}
                                                </span>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </section>
</template>
  
<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
    name: "abogado",
    created() {
        this.loadSelectedOptions();
        this.listLawyers();
        this.listFiles();
    },
    beforeDestroy() {
        this.saveSelectedOptions(); // Guardar opciones seleccionadas antes de destruir el componente
        this.clearLocalStorage(); // Limpiar localStorage al cambiar de vista
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            dataTable: null,
            assignedAttorney: "default_value",
            newlawyer: "default_value",
            lawyersWithFiles: null,
            lawyers: null,
            files: null

        };
    },

    methods: {
        ...mapActions(["get", "post"]),

        // validarCampo(campo, propiedadError) {
        //     if (this.juzgado[campo] === "") {
        //         this.errores[propiedadError] = true;
        //     } else {
        //         this.errores[propiedadError] = false;
        //     }
        // },

        // validarNombre() {
        //     this.juzgado.co_nombre = this.juzgado.co_nombre;
        //     this.validarCampo('co_nombre', 'co_nombre');
        // },

        // validarCampos() {
        //     this.validarNombre();
        //     return !Object.values(this.errores).some(error => error);
        // },

        saveSelectedOptions() {
            // Guardar opciones seleccionadas en el localStorage
            localStorage.setItem('assignedAttorney', this.assignedAttorney);
            localStorage.setItem('newlawyer', this.newlawyer);
        },
        loadSelectedOptions() {
            // Cargar opciones seleccionadas desde el localStorage
            const assignedAttorney = localStorage.getItem('assignedAttorney');
            const newlawyer = localStorage.getItem('newlawyer');

            if (assignedAttorney !== null && newlawyer !== null) {
                this.assignedAttorney = assignedAttorney;
                this.newlawyer = newlawyer;
            }
        },
        clearLocalStorage() {
            // Limpiar localStorage al cambiar de vista
            localStorage.removeItem('assignedAttorney');
            localStorage.removeItem('newlawyer');
        },

        completeNames(lawyer) {
            return `${lawyer.nat_nombres} ${lawyer.nat_apellido_paterno} ${lawyer.nat_apellido_materno}`;
        },

        badgeClass(estado) {
            return {
                'badge tramite p-2 rounded': estado === 'En Tramite',
                'badge ejecucion p-2 rounded': estado === 'En Ejecucion',
            };
        },

        getPersonaName(file) {
            if (file.tipo_persona === 'natural') {
                return `${file.apellido_paterno} ${file.apellido_materno} ${file.nombres}`;
            } else {
                return file.razon_social;
            }
        },

        delay(ms) {
            return new Promise(resolve => setTimeout(resolve, ms));
        },

        async listLawyers() {
            try {
                const response = await this.get({
                    url: this.$store.getters.get__url + "/lawyer",
                    token: this.$store.getters.get__token,
                });

                this.lawyers = response.data;
                this.lawyersWithFiles = response.data.filter(lawyerWith => lawyerWith.abo_carga_laboral > 0);
                console.log(this.lawyersWithFiles);
                console.log(response);
            } catch (error) {
                console.error("Error al intentar obtener los datos", error);
            }
        },

        async listFiles() {
            console.log('abogado-asignado:', this.assignedAttorney);
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/lawyer/expedientes",
                    token: this.$store.getters.get__token,
                    params: {
                        abo_id: this.assignedAttorney
                    }
                });
                this.saveSelectedOptions();
                console.log('files', response);
                this.files = response.data;
                this.initDataTable();
            } catch (error) {
                console.error(error);
            }
        },

        async saveChanges() {
            // if (!this.validarCampos()) {
            //     console.error('Error en la validacion de los campos de registro.');
            //     return;
            // }
            try {
                const filesShown = this.files.map(file => file.exp_id);
                const response = await this.post({
                    url: this.$store.getters.get__url + "/lawyer/changeOfLawyer",
                    token: this.$store.getters.get__token,
                    params: {
                        expedientes: filesShown,
                        abogado_actual: this.assignedAttorney,
                        abogado_nuevo: this.newlawyer
                    }
                });
                if (response.state == 0) {
                    this.assignedAttorney = "default_value";
                    this.newlawyer = "default_value";
                    await this.delay(500);

                    console.log(response);
                    await this.listLawyers();
                    await this.listFiles();
                    
                } else {
                    console.log(response);
                }
            } catch (error) {
                console.error(error);
            }
        },

        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }

            this.$nextTick(() => {
                this.dataTable = $('#miTabla').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
                feather.replace();
            });
        },
    },
};
</script>
  
<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}

.tramite {
    background-color: #fff6e5;
    color: #F4BD50;
    border: 1px solid #F4BD50;
}

.ejecucion {
    background-color: #EAFEF3;
    color: #2ecc71;
    border: 1px solid #2ecc71;
}

.badge {
    font-size: 13px !important;
}

.circle {
    width: 30px;
    height: 30px;
    background-color: #7267EF;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
}

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    padding-left: 15px;
    color: #EA4D4D;
    font-size: 12px;
}
.fade-transition {
    transition: opacity 0.5s ease; /* Duración de la transición: 0.5 segundos, easing: ease */
}

.fade-enter, .fade-leave-to /* .fade-leave-active en <2.1.8 */ {
    opacity: 0; /* Establecer la opacidad inicial y final para la animación de entrada y salida */
}
</style>
  